import React from "react";

import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';
import Img from 'gatsby-image';

import Layout from "../../components/layout_inner";

export default (props) => {
  const data = useStaticQuery(graphql`
    query {
        file(relativePath: { eq: "posts/expecting.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
    }
  `);

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | На каком сроке лучше фотографироваться в ожидании чуда?" defer={false} />
      <h1>
        На каком сроке лучше фотографироваться в ожидании чуда?
      </h1>
      <p>&nbsp;</p>
      <div class="post">
        <div class="post-content">
          <div class="row">
            <div>
              <Img style={{ width: "100%" }} fluid={data.file.childImageSharp.fluid} />
            </div>
            <div>
              <p>
                Конечно, все довольно индивидуально, т.к. у каждой будущей мамы растет животик по-разному.
              </p>
              <p>
                Если у вас первая беременность, то самый лучший срок - 32-34 недели, если вторая или двойня - то начиная с 26 недели беременности.
                Но, если вы интенсивно набираете вес, и у вас уже хорошо виден животик, можно проводить фотосессию и раньше.
              </p>
              <p>
                Вот чего не советую делать, так это затягивать. Потому что, во-первых, можно не успеть (т.е. мы определимся с датой проведения съёмки, а вы родите на пару недель раньше), а во- вторых, на последних неделях будущей мамочке очень тяжело физически.
              </p>
              <p>
                Поэтому, исходя из состояния лучше раньше, чем позже.
              </p>
            </div>
          </div>
        </div>
      </div>
      <div style={{ paddingBottom: "3%" }} className="clear"></div>
    </Layout>
  );
};
